"use client"

import { useEffect, useState, useCallback, useRef } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { 
  Send, User, MessageSquare, Loader2, Hash, ArrowLeft
} from "lucide-react"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"

import { Client } from '@stomp/stompjs'
import SockJS from 'sockjs-client'

export default function RealTimeChatPage() {
  const router = useRouter()
  
  const [userId, setUserId] = useState("")
  const [userName, setUserName] = useState("")
  const [activeTab, setActiveTab] = useState("GROUP") 
  const [rooms, setRooms] = useState<any[]>([])
  const [currentRoom, setCurrentRoom] = useState<any>(null)
  const [messages, setMessages] = useState<any[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isConnected, setIsConnected] = useState(false)
  
  const [isLoadingHistory, setIsLoadingHistory] = useState(false)
  const [hasMoreMessages, setHasMoreMessages] = useState(true)
  const [oldestMessageId, setOldestMessageId] = useState<string | null>(null)
  const [oldestCreatedAt, setOldestCreatedAt] = useState<string | null>(null)

  const stompClient = useRef<Client | null>(null)
  const scrollRef = useRef<HTMLDivElement>(null)
  const subscriptionRef = useRef<any>(null)

  const API_BASE = 'http://sparta-nextme.xyz:18080'

  // ★ 추가: 서버의 createdAt을 화면용 시간으로 변환하는 함수
  const formatTime = (dateString: string) => {
    if (!dateString) return "";
    const date = new Date(dateString);
    return date.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit', hour12: false });
  }

  const loadRooms = useCallback(async (type: string) => {
    const token = localStorage.getItem("accessToken")
    if (!token) return
    try {
      const res = await fetch(`${API_BASE}/v1/chats/room?roomType=${type}`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      const roomList = data.rooms || data.result || (Array.isArray(data) ? data : [])
      setRooms(roomList)
    } catch (e) { setRooms([]) }
  }, [])

  // 채팅 내역 로드 (무한 스크롤 포함)
  const loadChatHistory = async (roomId: string, isMore = false) => {
    if (isLoadingHistory) return;
    const token = localStorage.getItem("accessToken");
    
    // 기본 URL
    let url = `${API_BASE}/v1/chats/message/${roomId}?size=20`;
    
    // ★ 수정: 무한 스크롤 시 이전에 저장된 oldestMessageId와 oldestCreatedAt을 query로 전달
    if (isMore && oldestMessageId && oldestCreatedAt) {
      url += `&beforeMessageId=${oldestMessageId}&beforeCreatedAt=${encodeURIComponent(oldestCreatedAt)}`;
    }
    
    setIsLoadingHistory(true);
    try {
      const res = await fetch(url, { headers: { "Authorization": `Bearer ${token}` } });
      const data = await res.json();
      const newMessages = Array.isArray(data) ? data : (data.messages || data.result || []);
      
      if (newMessages.length > 0) {
        // ★ 수정: 배열의 마지막(가장 오래된) 메시지 정보를 상태에 저장하여 다음 API 호출 시 사용
        const oldest = newMessages[newMessages.length - 1];
        setOldestMessageId(oldest.messageId?.chatMessageId || oldest.chatMessageId);
        setOldestCreatedAt(oldest.createdAt);
        
        if (isMore) {
          // 이전 데이터 로딩 시 현재 메시지들 "앞에" 추가
          setMessages(prev => [...[...newMessages].reverse(), ...prev]);
        } else {
          // 첫 진입 시
          setMessages([...newMessages].reverse());
          scrollToBottom();
        }
      }
      setHasMoreMessages(newMessages.length === 20);
    } catch (e) { console.error(e) } finally { setIsLoadingHistory(false) }
  }

  const subscribeToRoom = (roomId: string) => {
    if (!stompClient.current || !stompClient.current.connected) return;
    if (subscriptionRef.current) subscriptionRef.current.unsubscribe();

    const destination = `/topic/chat.room.${roomId}`;
    subscriptionRef.current = stompClient.current.subscribe(destination, (message) => {
        const messageData = JSON.parse(message.body);
        setMessages((prev) => [...prev, messageData]);
        
        setRooms(prevRooms => prevRooms.map(r => {
          const rId = r.chatRoomId || r.id;
          if (rId === roomId) return { ...r, lastMessage: messageData.content };
          return r;
        }));
        scrollToBottom();
    });
  };

  const selectRoom = async (room: any) => {
    const roomId = room.chatRoomId || room.id;
    if (currentRoom?.chatRoomId === roomId || currentRoom?.id === roomId) return;
    
    const token = localStorage.getItem("accessToken");
    setCurrentRoom(room);
    setMessages([]); 
    // 방 변경 시 기준점 초기화
    setOldestMessageId(null);
    setOldestCreatedAt(null);

    try {
      await fetch(`${API_BASE}/v1/chats/room/${roomId}/join`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${token}` }
      });
      subscribeToRoom(roomId);
      await loadChatHistory(roomId);
    } catch (e) { console.error(e) }
  }

  const handleSendMessage = (e?: React.FormEvent) => {
    if (e) e.preventDefault();
    if (!inputValue.trim() || !stompClient.current?.connected || !currentRoom) return;
    const roomId = currentRoom.chatRoomId || currentRoom.id;
    stompClient.current.publish({
      destination: `/app/chat.send/${roomId}`,
      body: JSON.stringify({ 
        content: inputValue, 
        roomType: currentRoom.roomType || currentRoom.type || activeTab 
      }),
    });
    setInputValue("");
  }

  const handleScroll = () => {
    if (scrollRef.current && scrollRef.current.scrollTop < 50 && hasMoreMessages && !isLoadingHistory) {
      const roomId = currentRoom?.chatRoomId || currentRoom?.id;
      if (roomId) loadChatHistory(roomId, true);
    }
  };

  useEffect(() => {
    const token = localStorage.getItem("accessToken");
    if (!token) return router.push("/login");
    setUserId(localStorage.getItem("userId") || "");
    setUserName(localStorage.getItem("name") || "사용자");

    const client = new Client({
      brokerURL: `ws://sparta-nextme.xyz:18080/ws/chat`,
      connectHeaders: { Authorization: `Bearer ${token}` },
      webSocketFactory: () => new SockJS(`${API_BASE}/ws/chat`),
      reconnectDelay: 5000,
      onConnect: () => { 
        setIsConnected(true); 
        loadRooms(activeTab); 
      },
      onDisconnect: () => setIsConnected(false)
    });
    client.activate();
    stompClient.current = client;
    return () => { if (stompClient.current) stompClient.current.deactivate() }
  }, [activeTab, loadRooms, router]);

  const scrollToBottom = () => {
    setTimeout(() => { if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight }, 100)
  }

  return (
    <div className="flex h-screen w-screen bg-background text-foreground overflow-hidden font-sans">
      <aside className="w-80 md:w-96 flex flex-col bg-card border-r border-border shadow-2xl z-20">
        <div className="p-6 space-y-6">
            <div className="flex items-center gap-4">
              <Button variant="secondary" size="icon" onClick={() => router.push("/dashboard")} className="h-10 w-10 rounded-xl bg-secondary/50 shadow-sm"><ArrowLeft className="h-5 w-5" /></Button>
              <h1 className="text-lg font-black tracking-tight">메시지</h1>
            </div>
          <Tabs value={activeTab} onValueChange={(v) => { setActiveTab(v); setCurrentRoom(null); loadRooms(v); }}>
            <TabsList className="grid grid-cols-2 w-full bg-secondary/50 h-11 rounded-xl">
              <TabsTrigger value="GROUP" className="rounded-lg font-bold text-xs">그룹 채널</TabsTrigger>
              <TabsTrigger value="DIRECT" className="rounded-lg font-bold text-xs">1:1 메시지</TabsTrigger>
            </TabsList>
          </Tabs>
        </div>
        <div className="flex-1 overflow-y-auto px-4 pb-6 space-y-2 custom-scrollbar">
          {rooms.map((room: any) => {
            const isSelected = (currentRoom?.chatRoomId || currentRoom?.id) === (room.chatRoomId || room.id);
            let lastMsgText = room.lastMessage || "대화가 없습니다.";
            
            return (
              <button key={room.chatRoomId || room.id} onClick={() => selectRoom(room)} className={`w-full p-4 rounded-2xl flex items-center gap-4 transition-all ${isSelected ? 'bg-primary/10 ring-1 ring-primary/30 shadow-md' : 'hover:bg-secondary/30'}`}>
                <div className={`h-11 w-11 rounded-2xl flex items-center justify-center ${isSelected ? 'bg-primary text-primary-foreground' : 'bg-secondary text-muted-foreground'}`}>
                  {activeTab === "GROUP" ? <Hash className="h-5 w-5" /> : <User className="h-5 w-5" />}
                </div>
                <div className="text-left overflow-hidden flex-1">
                  <p className={`font-bold truncate text-[14px] ${isSelected ? 'text-primary' : ''}`}>{room.title || room.name}</p>
                  <p className="text-[11px] text-muted-foreground truncate font-medium mt-0.5">{lastMsgText}</p>
                </div>
              </button>
            )
          })}
        </div>
      </aside>

      <main className="flex-1 flex flex-col bg-background relative">
        {currentRoom ? (
          <>
            <header className="h-[76px] px-8 border-b border-border flex items-center justify-between bg-card/30 backdrop-blur-xl z-10">
              <div className="flex items-center gap-4">
                <div className="h-11 w-11 rounded-2xl bg-secondary/50 flex items-center justify-center text-primary">
                  {activeTab === "GROUP" ? <Hash className="h-5 w-5" /> : <User className="h-5 w-5" />}
                </div>
                <h2 className="font-black text-base tracking-tight">{currentRoom.title || currentRoom.name}</h2>
              </div>
            </header>

            <div ref={scrollRef} onScroll={handleScroll} className="flex-1 overflow-y-auto px-8 py-8 space-y-6 custom-scrollbar bg-gradient-to-b from-background to-secondary/5">
              {isLoadingHistory && <div className="text-center py-2"><Loader2 className="h-5 w-5 animate-spin mx-auto text-muted-foreground" /></div>}
              {messages.map((msg, idx) => {
                const isSystem = msg.type === "ENTER" || msg.senderName === "[System]";
                if (isSystem) {
                  return (
                    <div key={idx} className="flex justify-center my-6">
                      <div className="bg-secondary/40 text-muted-foreground text-[11px] font-black px-6 py-2 rounded-full border border-border/50 shadow-sm">
                        {userName}님이 {msg.content}
                      </div>
                    </div>
                  )
                }

                const isMine = String(msg.senderId) === String(userId);
                
                // ★ 수정: 서버에서 내려온 msg.createdAt을 사용하여 시간을 표시
                const time = formatTime(msg.createdAt);

                return (
                  <div key={idx} className={`flex ${isMine ? "justify-end" : "justify-start"} animate-in fade-in slide-in-from-bottom-3 duration-500`}>
                    <div className={`flex flex-col max-w-[70%] ${isMine ? "items-end" : "items-start"}`}>
                      {!isMine && <span className="text-[11px] font-black text-muted-foreground/70 mb-1.5 ml-1">{msg.senderName}</span>}
                      <Card className={`p-4 text-sm font-medium border-none shadow-xl ${isMine ? "bg-primary text-primary-foreground rounded-2xl rounded-tr-none" : "bg-card text-foreground rounded-2xl rounded-tl-none ring-1 ring-border/50"}`}>
                        {msg.content}
                      </Card>
                      {/* ★ 시간 노출 부분 */}
                      {time && <span className="text-[9px] text-muted-foreground/40 font-bold mt-2 px-1 tracking-tighter">{time}</span>}
                    </div>
                  </div>
                )
              })}
            </div>

            <footer className="p-6 bg-card/30 backdrop-blur-lg border-t border-border">
              <form onSubmit={handleSendMessage} className="max-w-4xl mx-auto flex gap-3">
                <Input value={inputValue} onChange={(e) => setInputValue(e.target.value)} placeholder="메시지를 입력하세요..." className="flex-1 h-14 bg-secondary/50 border-none rounded-2xl pl-6" />
                <Button type="submit" size="icon" className="h-14 w-14 rounded-2xl bg-primary shadow-lg" disabled={!inputValue.trim()}><Send className="h-5 w-5" /></Button>
              </form>
            </footer>
          </>
        ) : (
          <div className="flex-1 flex flex-col items-center justify-center p-12 text-center bg-background">
            <MessageSquare className="h-10 w-10 text-muted-foreground/20 mb-8" />
            <h3 className="text-xl font-black tracking-tight">대화 상대를 선택하세요</h3>
          </div>
        )}
      </main>

      <style jsx global>{`
        .custom-scrollbar::-webkit-scrollbar { width: 4px; }
        .custom-scrollbar::-webkit-scrollbar-thumb { background: hsl(var(--secondary)); border-radius: 10px; }
      `}</style>
    </div>
  )
}